  .gallery-parent-container {
    // border: 1px solid grey;
    margin: 0 1em 0;
    display: grid;
    grid-template-columns: repeat(23, minmax(15%, 25%));
    grid-template-rows: repeat(3, minmax(250px, 1fr));
    grid-gap: 13px;
    max-height: 800px;
    overflow-x: scroll;
    overflow-y: hidden;

    img {
      max-width: 100%;
      max-height: 100%;
    }
